import { renderToString } from 'katex';

// 公式内容的边距
export const CONTENT_MARGIN = 4;

const ERROR_COLOR = '#FF5219';

export const FORMULA_PLACEHOLDER_WIDTH = 98;
export const FORMULA_PLACEHOLDER_HEIGHT = 32;
export const FORMULA_PREVIEW_MIN_WIDTH = 8;
export const FORMULA_PREVIEW_MIN_HEIGHT = 32;

export const createPlaceholderSize = () => ({
  width: FORMULA_PLACEHOLDER_WIDTH,
  height: FORMULA_PLACEHOLDER_HEIGHT,
});

export function getKatexString(text: string, throwOnError = false) {
  return renderToString(text, {
    errorColor: ERROR_COLOR,
    throwOnError,
  });
}

export function calcuSize(formula: string) {
  if (!formula) {
    return createPlaceholderSize();
  }

  const size = measureFormula(formula);
  const width = getValue(FORMULA_PREVIEW_MIN_WIDTH, CONTENT_MARGIN * 2 + size.width);
  const height = getValue(FORMULA_PREVIEW_MIN_HEIGHT, CONTENT_MARGIN * 2 + size.height);
  return { width, height };
}

/**
 * 返回一个不超过最小值和最大值的数字
 * @param min 最小值
 * @param value 计算值
 * @param max 最大值
 */
export function getValue(min: number, value: number, max?: number | null) {
  if (!max) {
    return Math.max(min, value);
  }
  return Math.min(Math.max(min, value), max);
}

/**
 * 返回公式内容的大小
 * @param text 公式字符串
 */
export function measureFormula(text: string) {
  const editorContent = document.querySelector('div[data-cangjie-content]');
  if (!editorContent) {
    return createPlaceholderSize();
  }

  const domString = getKatexString(text);
  const faker = document.createElement('div');
  faker.style.width = 'fit-content';
  faker.innerHTML = domString;
  editorContent.appendChild(faker);

  const { width, height } = faker.getBoundingClientRect();
  editorContent.removeChild(faker);

  return {
    width,
    height,
  };
}
